/*=============================
Preloader
===============================*/
#preloader {
  height: 100%;
  width: 100%;
  position: fixed;
  margin-top: 0px;
  top: 0px;
  z-index: 9999;
  background-color: var(--tc-system-black-2);
}
#preloader .loader .loader-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  border: 3px solid #fdf2f9;
  border-radius: 50%;
}
#preloader .loader .loader-container::before {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  border-top: 3px solid var(--tc-theme-primary);
  border-radius: 50%;
  animation: loaderspin 1.8s infinite ease-in-out;
  -webkit-animation: loaderspin 1.8s infinite ease-in-out;
}
#preloader .loader .loader-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  text-align: center;
}
#preloader .loader .loader-icon img {
  animation: loaderpulse alternate 900ms infinite;
  width: 40px;
}
#preloader.black-bg .loader-container, #preloader.black2-bg .loader-container {
  border: 3px solid #1a1a1a;
}
#preloader.black-bg .loader-icon, #preloader.black2-bg .loader-icon {
  filter: brightness(99) grayscale(9);
}

@keyframes loaderspin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@-webkit-keyframes loaderspin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
@keyframes loaderpulse {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}
.tg-img-reveal[data-fx="1"]::after,
.tg-img-reveal[data-fx="2"]::after {
  content: "";
  z-index: -1;
  width: 100%;
  bottom: 0.25rem;
  left: 0;
  position: absolute;
  height: 2px;
  background: currentColor;
  transform: scale3d(0, 1, 1);
  transform-origin: 0% 50%;
  transition: transform 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
  text-align: center;
}

.tg-img-reveal[data-fx="1"]::after,
.tg-img-reveal[data-fx="2"]::after {
  bottom: 0;
}

.tg-img-reveal[data-fx="1"]:hover::after,
.tg-img-reveal[data-fx="2"]:hover::after {
  transform: scale3d(1, 1, 1);
}

.tg-img-reveal[data-fx="6"]::after {
  content: "";
  z-index: -1;
  width: 100%;
  bottom: 0.25rem;
  left: 0;
  position: absolute;
  height: 2px;
  background: currentColor;
  transform: scale3d(0, 1, 1);
  transform-origin: 100% 50%;
  transition: transform 0.7s cubic-bezier(0.86, 0, 0.07, 1);
}

.tg-img-reveal[data-fx="6"]::after {
  bottom: 0;
}

.tg-img-reveal[data-fx="6"]:hover::after {
  transform: scale3d(1, 1, 1);
}

.tg-img-reveal[data-fx="14"]::after {
  content: "";
  z-index: -1;
  width: 100%;
  bottom: 0.25rem;
  left: 0;
  position: absolute;
  height: 2px;
  background: currentColor;
  transform: scale3d(0, 1, 1);
  transform-origin: 0% 50%;
}

.tg-img-reveal[data-fx="14"]::after {
  bottom: 0;
}

.tg-img-reveal[data-fx="14"]:hover::after {
  animation: loop 0.5s infinite;
}

@keyframes loop {
  0% {
    transform-origin: 0% 50%;
    transform: scale3d(0, 1, 1);
  }
  50% {
    transform-origin: 0% 50%;
    transform: scale3d(1, 1, 1);
  }
  51% {
    transform-origin: 100% 50%;
  }
  100% {
    transform-origin: 100% 50%;
    transform: scale3d(0, 1, 1);
  }
}
